export default "<template>\n  <div>\n    <div class=\"section\">\n      <h1>Virtual Items</h1>\n      <p>UI for face masks items and grid.</p>\n\n      <pre><code>import { ItemGrid, VirtualItem } from \"streamlabs-beaker\"\n\ncomponents: {\n  ItemGrid,\n  VirtualItem\n}</code></pre>\n\n      <div class=\"section\">\n        <h2>Giveaway Items</h2>\n        <DemoSection title=\"Giveaway Items\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"container\">\n              <VirtualItem\n                :preview=\"itemImage\"\n                rarity=\"Enter Giveaway\"\n                name=\"4x VIP Tickets to PAX, TwitchCon and SXSW\"\n                remainingTime=\"2h left\"\n                :hasWarning=\"true\"\n              />\n\n              <VirtualItem\n                :preview=\"itemImage\"\n                rarity=\"Entered\"\n                name=\"$5K Custom High-Spec Streaming Desktop PC\"\n                remainingTime=\"5d left\"\n                :isGiveaway=\"true\"\n              />\n            </div>\n          </template>\n        </DemoSection>\n      </div>\n\n      <h2>Face Masks</h2>\n      <DemoSection title=\"Face Masks\" :code=\"demoCode\">\n        <template #components>\n          <ItemGrid>\n            <VirtualItem\n              v-for=\"(mask, id) in masks\"\n              :key=\"id\"\n              :preview=\"mask.preview\"\n              :rarity=\"mask.rarity\"\n              :name=\"mask.name\"\n              :selectionCount=\"mask.selectionCount\"\n              :selected=\"mask.selected\"\n              @click=\"clickEvent\"\n            />\n          </ItemGrid>\n        </template>\n      </DemoSection>\n    </div>\n\n    <table class=\"docs-table\">\n      <thead>\n        <tr>\n          <th>Props</th>\n          <th>Type</th>\n          <th>Default</th>\n          <th>Description</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr>\n          <td>name</td>\n          <td>string</td>\n          <td>null</td>\n          <td>Name of the item.</td>\n        </tr>\n        <tr>\n          <td>value</td>\n          <td>string</td>\n          <td>null</td>\n          <td>Value you want to assign to the item.</td>\n        </tr>\n        <tr>\n          <td>preview</td>\n          <td>string</td>\n          <td>null</td>\n          <td>Item preview image.</td>\n        </tr>\n        <tr>\n          <td>quantity</td>\n          <td>number</td>\n          <td>null</td>\n          <td>Quantity of the particular item.</td>\n        </tr>\n        <tr>\n          <td>rarity</td>\n          <td>string</td>\n          <td>null</td>\n          <td>\n            Gives item rarity class <code>s-virtual-item--${rarity}</code> and\n            adds rarity string to item. Use for giveaway text.\n          </td>\n        </tr>\n        <tr>\n          <td>selected</td>\n          <td>boolean</td>\n          <td>false</td>\n          <td>Sets item to selected state.</td>\n        </tr>\n        <tr>\n          <td>selectionCount</td>\n          <td>string</td>\n          <td>null</td>\n          <td>\n            Adds counter to top right of item. You have to set this\n            functionality.\n          </td>\n        </tr>\n        <tr>\n          <td>remainingTime</td>\n          <td>string</td>\n          <td>null</td>\n          <td>Remaining time for giveaways or special promotions</td>\n        </tr>\n        <tr>\n          <td>hasWarning</td>\n          <td>boolean</td>\n          <td>false</td>\n          <td>Changes item background color to red.</td>\n        </tr>\n        <tr>\n          <td>isGiveaway</td>\n          <td>boolean</td>\n          <td>false</td>\n          <td>Adds checkmark and set rarity color to teal.</td>\n        </tr>\n        <tr>\n          <td>type</td>\n          <td>string</td>\n          <td>null</td>\n          <td>Add type text to top of item</td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\n\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport ItemGrid from \"./../components/ItemGrid.vue\";\nimport VirtualItem from \"./../components/VirtualItem.vue\";\nimport VirtualItemsCode from \"./VirtualItems.vue?raw\";\n\n@Component({\n  components: {\n    DemoSection,\n    ItemGrid,\n    VirtualItem\n  }\n})\nexport default class VirtualItems extends Vue {\n  itemImage = 'https://www.pngmart.com/files/21/3D-Download-PNG-Image.png';\n  demoCode = VirtualItemsCode;\n  masks = [\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\",\n      selectionCount: \"1\",\n      selected: true\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\",\n      selectionCount: \"2\",\n      selected: true\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\",\n      selectionCount: \"3\",\n      selected: true\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\"\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\"\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\"\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\"\n    },\n    {\n      preview: this.itemImage,\n      name: \"Robot\",\n      rarity: \"Common\"\n    }\n  ];\n\n  clickEvent() {\n    console.log(\"Hey you clicked me!\");\n  }\n}\n</script>\n\n<style lang=\"less\" scoped>\n.container {\n  display: grid;\n  grid-template-columns: 240px 1fr;\n}\n</style>\n"